body.no-scroll {
    overflow: hidden;
}

button.btn {
    background: #ffffff;
    border: #d9d9d9 1px solid;
    box-shadow: 0 2px 0 rgba(0, 0, 0, 0.02);
    font-size: 14px;
    height: 32px;
    padding: 4px 15px;
    border-radius: 6px;
    outline: none;
    text-align: center;
    cursor: pointer;
    transition: all 0.4s linear;
    display: flex;
    align-items: center;
    justify-content: center;
}

button.btn span {
    margin-left: 5px;
}

button.btn.selected {
    color: #ffffff !important;
    background: #1677ff !important;
}

button.btn.primary {
    color: #ffffff;
    background: #1677ff;
    box-shadow: 0 2px 0 rgba(5, 145, 255, 0.1);
}

button.btn.ghost {
    background: transparent;
    box-shadow: none;
}

button.btn.text {
    border: none;
}

button.btn.dashed {
    border-style: dashed;
}

button.btn:not(:disabled):hover,
button.btn.ghost:not(:disabled):hover,
button.btn.dashed:not(:disabled):hover {
    color: #4096ff;
    border-color: #4096ff;
}

button.btn.primary:not(:disabled):hover,
button.btn.text:not(:disabled):hover {
    color: #ffffff;
    background: #4096ff;
}

button.btn.circle-radius {
    border-radius: 50%;
    padding: 10px;
    height: 35px;
    width: 35px;
}

button.btn.round-radius {
    border-radius: 40px;
}

button.btn:disabled {
    cursor: auto;
}

input.inp {
    width: 100%;
    border: #d9d9d9 1px solid;
    box-shadow: 0 2px 0 rgba(0, 0, 0, 0.02);
    font-size: 14px;
    height: 32px;
    padding: 4px 15px;
    border-radius: 6px;
    outline: none;
    background-color: transparent;
    transition: all 0.4s linear;
}

input.inp.search {
    height: 48px;
    font-size: 18px;
    background: #ffffff;
}

input.inp:focus {
    border: 1px #4096ff solid;
}

.rating-mini {
