.checkbox-wrapper-33 {
    --s-xsmall: 0.625em;
    --s-small: 1.2em;
    --border-width: 1px;
    --c-primary: #4096ff;
    --c-primary-20-percent-opacity: rgb(17 103 232 / 20%);
    --c-primary-10-percent-opacity: rgb(17 103 232 / 10%);
    --t-base: 0.4s;
    --t-fast: 0.2s;
    --e-in: ease-in;
    --e-out: cubic-bezier(0.11, 0.29, 0.18, 0.98);
}

.checkbox-wrapper-33 .visuallyhidden {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

.checkbox-wrapper-33 .checkbox {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    cursor: pointer;
    width: 400px;
    user-select: none;
}
.checkbox-wrapper-33 .checkbox + .checkbox {
    margin-top: var(--s-small);
}
.checkbox-wrapper-33 .checkbox__symbol {
    display: inline-block;
    display: flex;
    margin-right: calc(var(--s-small) * 0.7);
    border: var(--border-width) solid var(--c-primary);
    position: relative;
    border-radius: 0.1em;
    width: 16px;
    height: 16px;
    transition: box-shadow var(--t-base) var(--e-out), background-color var(--t-base);
    box-shadow: 0 0 0 0 var(--c-primary-10-percent-opacity);
}
.checkbox-wrapper-33 .checkbox__symbol:after {
    content: "";
    position: absolute;
    top: 0.5em;
    left: 0.5em;
    width: 0.25em;
    height: 0.25em;
    background-color: var(--c-primary-20-percent-opacity);
    opacity: 0;
    border-radius: 3em;
    transform: scale(1);
    transform-origin: 50% 50%;
}
.checkbox-wrapper-33 .checkbox .icon-checkbox {
    width: 1em;
    height: 1em;
    margin: auto;
    fill: none;
    stroke-width: 3;
    stroke: currentColor;
    stroke-linecap: round;
    stroke-linejoin: round;
    stroke-miterlimit: 10;
    color: var(--c-primary);
    display: inline-block;
}
.checkbox-wrapper-33 .checkbox .icon-checkbox path {
    transition: stroke-dashoffset var(--t-fast) var(--e-in);
    stroke-dasharray: 30px, 31px;
    stroke-dashoffset: 31px;
}
.checkbox-wrapper-33 .checkbox__trigger:checked + .checkbox__symbol:after {
    -webkit-animation: ripple-33 1.5s var(--e-out);
    animation: ripple-33 1.5s var(--e-out);
}
.checkbox-wrapper-33 .checkbox__trigger:checked + .checkbox__symbol .icon-checkbox path {
    transition: stroke-dashoffset var(--t-base) var(--e-out);
    stroke-dashoffset: 0px;
}
.checkbox-wrapper-33 .checkbox__trigger:focus + .checkbox__symbol {
    box-shadow: 0 0 0 0.25em var(--c-primary-20-percent-opacity);
}

.progressbar {
    counter-reset: step;
    display: flex;
    justify-content: center;
    margin: 0;
}
.progressbar li {
    list-style: none;
    display: inline-block;
    width: 30.33%;
    position: relative;
    text-align: center;
}
.progressbar li:before {
    content: counter(step);
    counter-increment: step;
    width: 30px;
    height: 30px;
    line-height: 30px;
    border: 1px solid #ddd;
    border-radius: 100%;
    display: block;
    text-align: center;
    margin: 0 auto;
    background-color: #fff;
}
.progressbar li:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    background-color: #ddd;
    top: 15px;
    left: -50%;
    z-index: -1;
}
.progressbar li:first-child:after {
    content: none;
}
.progressbar li.active {
    color: #4096ff;
}
.progressbar li.active:before {
    border-color: #4096ff;
}
.progressbar li.active + li:after {
    background-color: #4096ff;
}

.register-box {
    width: 100%;
    padding: 15px 40px 0px 40px;
    box-sizing: border-box;
}

.register-box h2 {
    margin: 0 0 30px;
    padding: 0;
    color: black;
    text-align: center;
}

.register-box h3 {
    margin: 0 0 30px;
    padding: 0;
    color: black;
    text-align: center;
}

.register-box .user-box {
    position: relative;
}

.register-box .user-box input {
    width: 100%;
    padding: 10px 0;
    font-size: 16px;
    color: black;
    margin-bottom: 30px;
    border: none;
    border-radius: 4px;
    padding: 10px;
    border: 1px solid #4096ff;
    outline: none;
    background: transparent;
}
.register-box .user-box label {
    position: absolute;
    top: 0;
    left: 10px;
    padding: 10px 0;
    font-size: 16px;
    color: black;
    pointer-events: none;
    transition: 0.5s;
}

.register-box .user-box label.active {
    top: -10px;
    left: 20px;
    padding: 2px 10px;
    color: #4096ff;
    background: white;
    font-size: 12px;
}

.register-box .user-box input:focus ~ label,
.register-box .user-box input:valid ~ label:not(.unrequired) {
    top: -10px;
    left: 20px;
    padding: 2px 10px;
    color: #4096ff;
    background: white;
    font-size: 12px;
}

.register-box .user-box .error-message {
    position: absolute;
    top: 32px;
    left: 20px;
    padding: 0px 0;
    font-size: 16px;
    color: red;
    pointer-events: none;
    transition: 0.5s;
    background: white;
}

html,
body {
    scrollbar-color: #4096ff transparent;
    scrollbar-width: 5px;
}

html::-webkit-scrollbar {
    width: 5px;
    height: 10px;
    background-color: transparent;
}

html::-webkit-scrollbar-thumb {
    background-color: #4096ff;
}

@-webkit-keyframes ripple-33 {
    from {
        transform: scale(0);
        opacity: 1;
    }
    to {
        opacity: 0;
        transform: scale(20);
    }
}

@keyframes ripple-33 {
    from {
        transform: scale(0);
        opacity: 1;
    }
    to {
        opacity: 0;
        transform: scale(20);
    }
}

@media screen and (max-width: 640px) {
    .checkbox-wrapper-33 .checkbox {
        width: 100%;
    }

    .progressbar {
        padding: 10px 0 20px 0;
    }

    .register-box {
        width: 100%;
        padding: 20px 25px 0px 10px;
    }
}
