.filters-container {
    padding: 20px;
    background: var(--light);
}

.filters-container-wrapper {
    max-width: 600px;
    margin: 0 auto;
}

.filters-container input {
    margin-bottom: 20px;
}

.filters-container-actions {
    display: flex;
}

.filters-container-actions button:nth-child(even) {
    margin-left: 10px;
}

.dropdown {
    position: relative;
    margin-top: 10px;
    margin-bottom: 20px;
    width: 100%;
    height: 48px;
    border: #d9d9d9 1px solid;
    border-radius: 6px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: all 0.4s linear;
}

option,
select {
    font-size: 14px;
    color: #505050;
    padding-left: 15px;
    border: none;
}

.dropdown select::-webkit-scrollbar {
    width: 5px;
    height: 10px;
    padding: 0;
    margin: 0;
    background-color: transparent;
}

.dropdown select::-webkit-scrollbar-thumb {
    background-color: #4096ff;
    padding: 0;
    margin: 0;
}

.dropdown:focus,
.dropdown:hover {
    border: 1px #4096ff solid;
    font-size: 16px;
    color: #505050;
}

.dropdown select {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    width: 100%;
    height: 48px;
    display: flex;
    background: none;
    font-size: 16px;
}

.dropdown select::-ms-expand {
    display: none;
}

