@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@500;600&display=swap");

:root {
    --dark: #1e2329;
    --light: #fafafa;
    --success: #03a66d;
    --danger: #cf304a;
}

* {
    font-family: "Open Sans", sans-serif;
}

body,
p {
    margin: 0;
}

body {
    height: 100vh;
}

body div#root-health {
    height: 100%;
}

.widget {
    width: 100%;
    height: 100%;
    position: relative;
}

.widget-container {
    margin: 0 auto;
    max-width: 600px;
}

.fullname {
    color: #b6b6b6;
    font-size: 14px;
    margin-top: 3px;
    font-weight: 500;
}

.doctor-item {
    position: relative;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 6px;
    background: var(--light);
    margin: 20px 0;
    -webkit-box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.5);
    -moz-box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.5);
    box-shadow: 4px 4px 20px 0px rgba(0, 0, 0, 0.5);
}

.doctor-item-photo {
    max-width: 250px;
}

.doctor-modal-main {
    display: flex;
    align-items: center;
    padding: 0 40px;
}

.doctor-modal-main .doctor-item-photo {
    max-width: 120px;
    margin-right: 15px;
}

.score-container {
    max-width: 220px;
    padding: 10px;
    display: flex;
    background: transparent;
    border: none;
    align-items: center;
    cursor: pointer;
    box-shadow: 0 2px 1px -1px rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 1px 3px 0 rgba(0, 0, 0, 0.12) !important;
}

.score-container p {
    margin: 0;
    padding-left: 5px;
    color: #0070c0;
}

.about-container {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.about-container h5 {
    margin: 5px 0;
}

.about-container .fullname {
    margin: 10px 0;
    color: black;
    font-size: 1rem;
    font-weight: 500;
    letter-spacing: 0;
    line-height: 135%;
}

.about-container .specialization {
    display: flex;
    max-width: 320px;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

.about-container .experience {
    margin: 10px 0;
    padding: 10px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #4e4e4e;
}

.places-container,
.directions-container {
    display: flex;
    padding: 0 20px;
    flex-direction: column;
    align-items: flex-start;
}

.doctor-item .places-container,
.doctor-item .directions-container {
    width: 100%;
}

.places-container .place {
    width: 100%;
    padding: 20px 0;
}

.places-container .place .place-description {
    font-size: 14px;
    color: #b6b6b6;
}

.directions-container .direction {
    padding: 10px 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #b6b6b6;
}

.directions-container .direction:last-child {
    border: none;
}

.directions-container .direction .direction-title {
    word-break: break-word;
    max-width: 80%;
    font-size: 14px;
}

.appointments-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.appointments-actions,
.appointments-times {
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.appointments-times {
    width: 100%;
    flex-wrap: wrap;
}

.appointments-times button {
    margin: 5px;
}

.appointments-actions span {
    padding: 0 20px;
}

.about-container .experience p {
    font-weight: 400;
    letter-spacing: 0;
    font-size: 0.875rem;
    line-height: 140%;
    color: white;
}

.about-container .specialization p {
    font-weight: 400;
    letter-spacing: 0;
    font-size: 0.875rem;
    line-height: 140%;
    color: #4e4e4e;
}

.about-container .specialization p:nth-child(even)::before {
    content: "â€¢";
    padding: 5px;
}

.about-container .experience p:nth-child(even)::before {
    content: "â€¢";
    padding: 5px;
}

.container {
    padding: 20px;
    border-radius: 12px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 30px;
    overflow: auto;
}

.icon {
    height: 24px;
    width: 24px;
    margin-right: 14px;
}

.price-container {
    margin-left: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.price {
    font-weight: 500;
    color: var(--light);
    font-size: 16px;
}

.price-change {
    margin-top: 3px;
}

.price-change.danger {
    color: var(--danger);
}

.price-change.success {
    color: var(--success);
}

.rating-mini {
    display: inline-block;
    font-size: 0;
}
.rating-mini span {
    padding: 0;
    font-size: 25px;
    line-height: 1;
    color: lightgrey;
}
.rating-mini > span:before {
    content: "â˜…";
}
.rating-mini > span.active {
    color: gold;
}

p.loading-text {
    margin: 20px;
    font-size: 18px;
    line-height: 140%;
    color: #4e4e4e;
}

.preloader {
    display: flex;
    justify-content: center;
}

.preloader svg {
    width: 100px;
    height: 100px;
    margin: 20px;
    display: inline-block;
}

@media screen and (max-width: 640px) {
    .doctor-item-photo {
        max-width: 150px;
    }

    .doctor-modal-main {
        flex-direction: column;
    }
}
