.modal-overlay {
    position: absolute;
    width: 90%;
    height: 95%;
    background: #ffffff;
    border-radius: 6px;
    -webkit-box-shadow: 4px 4px 20px 0px rgba(0, 0, 0, 0.5);
    -moz-box-shadow: 4px 4px 20px 0px rgba(0, 0, 0, 0.5);
    box-shadow: 4px 4px 20px 0px rgba(0, 0, 0, 0.5);
}

.modal-overlay.close {
    -webkit-animation: slide-out-elliptic-top-bck 0.7s ease-in both;
    animation: slide-out-elliptic-top-bck 0.7s ease-in both;
}

.modal-overlay.fullscreen {
    border-radius: 0;
    overflow: auto;
    bottom: 0;
    height: unset;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    width: auto;
    z-index: 100;
}

.modal-overlay.fullscreen::-webkit-scrollbar {
    width: 5px;
    height: 10px;
    padding: 0;
    margin: 0;
    background-color: transparent;
}

.modal-overlay.fullscreen::-webkit-scrollbar-thumb {
    background-color: #4096ff;
    padding: 0;
    margin: 0;
}

.modal-content {
    height: 100vh;
    padding: 0 10px;
    display: grid;
    grid-template-rows: 60px auto;
}

.modal-content .appointments-container {
    max-width: 600px;
}

.modal-content.modal-content-actions {
    grid-template-rows: 60px auto 80px;
}

.register-form {
    padding-top: 10px;
}

.register-form-actions {
    display: flex;
    padding-right: 10px;
    justify-content: flex-end;
}

.register-form-actions button {
    margin: 10px;
}

.modal-content-title {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
    border-bottom: 1px solid #4e4e4e;
}

.modal-content-title h2 {
    font-size: 28px;
    font-weight: 700;
    margin: 0;
}

.calendar .weekdays {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    margin-bottom: 20px;
    justify-items: center;
}

.calendar .dates {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    grid-row-gap: 20px;
    grid-auto-rows: 1fr;
    grid-column-gap: 0px;
    justify-items: center;
}

.calendar .navigation {
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.calendar-footer {
    margin: 0 20px;
    padding: 20px;
    font-size: 16px;
    color: #505050;
    text-align: center;
    border-bottom: 1px solid #a5a5a5;
}

.modal-actions {
    display: flex;
    padding: 0px 20px 20px 20px;
    display: flex;
    align-items: center;
}

.modal-actions button {
    flex: 1;
    height: 40px;
    margin: 20px;
}

.checkbox-group {
    display: flex;
    flex-direction: column;
    padding: 10px;
}

.checkbox-group p {
    color: black;
    font-size: 18px;
}

.checkbox-group span {
    color: #a5a5a5;
    font-size: 14px;
}

.calendar form {
    padding: 20px;
}

.calendar-form-title p {
    text-align: center;
    padding: 10px 0;
}

.doctor-modal-item form {
    padding: 0px 40px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.slide-open-center {
    -webkit-animation: slide-out-fwd-center 0.5s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
    animation: slide-out-fwd-center 0.5s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
    position: relative;
    z-index: 500;
}

@-webkit-keyframes slide-out-fwd-center {
    0% {
        -webkit-transform: scale3d(1, 1, 1);
        transform: scale3d(1, 1, 1);
        opacity: 1;
    }
    100% {
        -webkit-transform: scale3d(100, 100, 100);
        transform: scale3d(100, 100, 100);
        opacity: 0;
    }
}
@keyframes slide-out-fwd-center {
    0% {
        -webkit-transform: scale3d(1, 1, 1);
        transform: scale3d(1, 1, 1);
        opacity: 1;
    }
    100% {
        -webkit-transform: scale3d(100, 100, 100);
        transform: scale3d(100, 100, 100);
        opacity: 0;
    }
}

@media screen and (max-width: 640px) {
    .modal-content-title h2 {
        font-size: 20px;
    }

    .doctor-modal-item form {
        display: block;
    }
}

@media screen and (max-width: 420px) {
    .checkbox-group p {
        font-size: 16px;
    }

    .checkbox-group span {
        font-size: 12px;
    }

    .modal-actions button {
        margin: 10px;
    }

    .doctor-modal-item form {
        padding: 0px 10px;
    }
}

@-webkit-keyframes slide-out-elliptic-top-bck {
    0% {
        -webkit-transform: translateY(0) rotateX(0) scale(1);
        transform: translateY(0) rotateX(0) scale(1);
        -webkit-transform-origin: 50% 1400px;
        transform-origin: 50% 1400px;
    }
    100% {
        -webkit-transform: translateY(-600px) rotateX(-30deg) scale(0);
        transform: translateY(-600px) rotateX(-30deg) scale(0);
        -webkit-transform-origin: 50% 100%;
        transform-origin: 50% 100%;
    }
}
@keyframes slide-out-elliptic-top-bck {
    0% {
        -webkit-transform: translateY(0) rotateX(0) scale(1);
        transform: translateY(0) rotateX(0) scale(1);
        -webkit-transform-origin: 50% 1400px;
        transform-origin: 50% 1400px;
    }
    100% {
        -webkit-transform: translateY(-600px) rotateX(-30deg) scale(0);
        transform: translateY(-600px) rotateX(-30deg) scale(0);
        -webkit-transform-origin: 50% 100%;
        transform-origin: 50% 100%;
    }
}
